import React, { useRef, useEffect, useState } from "react";

import UniversalFilter from "./UniversalFilter";
import UniversalRodal from "./UniversalRodal";
import { useDispatch, useSelector } from "react-redux";
import YandexMap from "./yandexMap/YandexMap";
import UniversalTable from "./UniversalTable";
import { setEditingData, setModalClients } from "../redux/slices/clients/ClientSlice";
import { changeUrl } from "../redux/slices/dynamic items/table/tableSlice";
import { setLatitude, setLongitude } from "../redux/slices/Territory/TerritorySlice";
import { useForm } from "react-hook-form";
const Clients = () => {
  const dispatch = useDispatch();
  const loadingF = useSelector((state) => state.filter.loading);
  const { territory, modalClients, customerCategory,editingData } = useSelector(
    (state) => state.client
  );
  const { columnOrderModal, pageSize, tableActive } = useSelector(
    (state) => state.table
  );
  const getsData = useSelector((state) => state.territory.territory);
  const pagination = useSelector((state) => state.territory.allTerritorys);

  const {
    reset,
  } = useForm();

  useEffect(() => {
    dispatch(changeUrl("client/filter"));
  }, []);

  useEffect(() => {
    dispatch({
      type: "territory/getAllTeritory",
      payload: null,
    });
  }, [dispatch]);

  useEffect(() => {
    dispatch({
      type: "territory/getTerritory",
      payload: {
        pageSize: pageSize,
        page: 1,
        totalElements: pagination,
        active: tableActive,
        url: "client/filter",
      },
    });
  }, []);

  useEffect(() => {
    dispatch({
      type: "client/getAllCustomerCategory",
      payload: null,
    });
  }, [dispatch]);

  // rodal columns
  let clientsElement = [
    {
      name: "Territory",
      key: "territory",
      type: "select",
      defValue: "-",
      options: territory?.map((item) => ({
        label: item.name,
        value: item.id,
        
      })),
      required: true,
    },
    {
      name: "Category",
      key: "category",
      type: "select",
      defValue: "-",
      options: customerCategory?.map((item) => ({
        label: item.label,
        value: item.value,
      })),
      required: true,
    },
    {
      name: "Name",
      key: "name",
      type: "text",
      required: true,
    },
    {
      name: "Company name",
      key: "companyName",
      type: "text",
      required: true,
    },
    {
      name: "Address",
      key: "address",
      type: "text",
      required: true,
    },
    {
      name: "Reference point",
      key: "referencePoint",
      type: "text",
      required: true,
    },
    {
      name: "Telephone",
      key: "telephone",
      type: "tel",
      required: true,
    },
    {
      name: "Tin",
      key: "tin",
      type: "text",
      required: false,
    },
    {
      name: "active",
      key: "active",
      type: "checkbox",
      required: false,
    },
    {
      name: "save",
      key: "save",
      type: "jsx",
      data: (
        <div className="absolute bottom-9 left-4">
        <button className="bg-green-500 text-white mt-2 ml-1 py-2 px-4 hover:bg-green-700 rounded-md">
          save
        </button>
        </div>
      ),
    },
    {
      name: "Yandex",
      key: "map",
      type: "map",
      data: <YandexMap />,
    },
  ];



  // table columns
  let clientsTableColumns = [
    {
      title: "№",
      key: "id",
      dataType: "number",
      show: true,
    },
    {
      title: "Client name",
      key: "name",
      dataType: "text",
      show: true,
    },
    {
      title: "Company name",
      key: "company_name",
      dataType: "text",
      show: true,
    },
    {
      title: "Telephone",
      key: "phone",
      dataType: "text",
      show: true,
    },
    {
      title: "Territory",
      key: "territory",
      dataType: "text",
      show: true,
    },
    {
      title: "Address",
      key: "address",
      dataType: "text",
      show: true,
    },
    {
      title: "Reference Point",
      key: "reference_point",
      dataType: "text",
      show: true,
    },
    {
      title: "Category",
      key: "category",
      dataType: "text",
      show: true,
    },
    {
      title: "Activity",
      key: "active",
      dataType: "jsx",
      show: true,
      data: (item) => (
        <b className={`${item.active ? "text-green-500" : "text-black"}`}>
          {item.active ? "active" : "no active"}
        </b>
      ),
    },
    {
      title: "Date of registration",
      key: "registration_date",
      dataType: "text",
      show: true,
    },
    {
      title: "Action",
      key: "button",
      dataType: "jsx",
      show: true,
      data: (item) => (
        <div className="">
          <button
          className="bg-green-500 px-3 mx-auto hover:bg-green-600 p-[6px] grid place-items-center rounded-md text-white"
          onClick={() => updateClientHandle(item)}
        >
         edit
        </button>
        </div>
      ),
    },
  ];

  // filter columns

  let filterColumns = [
    {
      title: "Territories",
      dataType: "multi",
      urlValue: "cities",
      defaultValue: "Choose territories",
      values: territory?.map((item) => ({
        label: item.name,
        value: item.id,
      })),
    },
    {
      title: "TIN",
      dataType: "select",
      urlValue: "tin",
      defaultValue: "Choose TIN",
      values: [
        
        {
          label: "All",
          value: "",
        },
        {
          label: "With Tin",
          value: true,
        },
        {
          label: "Without Tin",
          value: false,
        },
      ],
    },
    {
      title: "Day",
      dataType: "opt",
      urlValue: "day",
      defaultValue: "Choose Day",
      values: [
        {
          label: "weeks",
          options: [
            { label: "All week", value: "ALL_WEEK", isDisabled: true },
            { label: "Every week", value: "EVERY_WEEK", isDisabled: true },
            { label: "Odd week", value: "ODD_WEEK", isDisabled: true },
            { label: "Even week", value: "EVEN_WEEK", isDisabled: true },
          ],
        },
        {
          label: "Days",
          options: [
            { label: "Dushanba", value: "Monday", isDisabled: true },
            { label: "Seshanba", value: "Tuesday", isDisabled: true },
            { label: "Chorshanba", value: "Wednesday", isDisabled: true },
            { label: "Payshanba", value: "Thursday", isDisabled: true },
            { label: "Juma", value: "Friday", isDisabled: true },
            { label: "Shanba", value: "Saturday ", isDisabled: true },
            { label: "Yakshanba", value: "Sunday ", isDisabled: true },
          ],
        },
      ],
    },
    {
      title: "Categories",
      dataType: "multi",
      urlValue: "categories",
      defaultValue: "Choose Categeries",
      values: customerCategory?.map((item) => ({
        label: item.label,
        value: item.value,
      })),
    },
  ];
  const modalRef = useRef(null);

  const handleUniversalModal = () => {
    dispatch(setModalClients(true));
  };

  // update clients

  const updateClientHandle = (item) => {
    dispatch(setModalClients(true));
    dispatch(setEditingData(item));
    dispatch({
      type: "client/updateCurrentClient",
      payload: item,
    });
     reset({
      territory: item.territory,
      category: item.category,
      name: item.name,
      companyName: item.company_name,
      address: item.address,
      telephone: item.phone,
      tin: item.tin,
      active: item.active,
      longitude: item.longitude,
      latitude: item.latitude,
    });
    dispatch(setLatitude(item.latitude));
    dispatch(setLongitude(item.longitude)); 
  };


  return (
    <div className="p-1">
      <h1 className="font-bold text-3xl px-2">Clients</h1>
      <div>
        <button
          ref={modalRef}
          onClick={handleUniversalModal}
          className="bg-green-500 ml-2 hover:bg-green-700 text-white  p-2 px-3 rounded-md"
        >
          add client
        </button>
      </div>
      <div>
        <UniversalFilter
          columns={filterColumns}
          loading={loadingF}
          url={"client/filter"}
          pageSize={pageSize}
          page={1}
          pagination={pagination}
          active={tableActive}
        />
        <UniversalRodal
          elements={clientsElement}
          onCloseModal={setModalClients(false)}
          modalChange={modalClients}
          url={"client"}
          data={editingData}
        />
        <UniversalTable
          data={getsData}
          columns={clientsTableColumns}
          modalChange={columnOrderModal}
          allData={pagination}
          url={"client/filter"}
          locName={"c_order"}
        />
      </div>
    </div>
  );
};

export default Clients;
