import { combineReducers } from "@reduxjs/toolkit";
import LoginReducer from "../slices/login/LoginSlice";
import DashboardReducer from "../slices/dashboard/DashboardSlice";
import TerritoryReducer from "./Territory/TerritorySlice";
import SettingsReducer from "./settings/SettingsSlice";
import UModalReducer from "./UModalSlice/UModalSlice";
import TableReducer from "./dynamic items/table/tableSlice";
import FilterReducer from "./dynamic items/filter/filterSlice";
import ClientReducer from "./clients/ClientSlice";
import CustumerCategoryReducer from "./CustomerCategory/CustomerCategorySlice"
import CompanyProfileReducer from "./CompanyProfile/CompanyProfileSlice"
import DragReducer from "./drag/DragSlice"

const rootReducer = combineReducers({
  // Add more reducers here if needed
  login: LoginReducer,
  dashboard: DashboardReducer,
  territory: TerritoryReducer,
  settings: SettingsReducer,
  uModal: UModalReducer,
  table: TableReducer,
  filter: FilterReducer,
  client: ClientReducer,
  customerCategory:CustumerCategoryReducer,
  companyProfile:CompanyProfileReducer,
  drag:DragReducer
});

export default rootReducer;
