import React, { useRef } from "react";
import { useDispatch,useSelector } from "react-redux";
import Select from "react-select";
import {
  saveFilterMap,
  sendFilterAxiosUrl,
} from "../redux/slices/dynamic items/filter/filterSlice";

const UniversalFilter = ({ columns, url,pageSize,pagination,active }) => {
  const dispatch = useDispatch();
  const selectRef = useRef();
  const  mapData  = useSelector((state) => state.filter.mapData);


  const showFilter = () => {
    let reuqestParamObj = mapData
    dispatch({
      type:"territory/getTerritory",
      payload:{
            pageSize: pageSize,
            page: 1,
            totalElements: pagination,
            active: active,
            url:url,
            param:reuqestParamObj
      }
    })

    if (Object.keys(reuqestParamObj).length === 0) {
      console.log(mapData.size);
      alert("filter tanlang!");
    } else {
      dispatch(sendFilterAxiosUrl(reuqestParamObj));
    }
  };

  const handleChange = (valueArr, action, urlValue) => {
    let multiArr = [];
    if (Array.isArray(valueArr)) {
      for (let i = 0; i < valueArr.length; i++) {
        multiArr.push(valueArr[i].value);
      }
      dispatch(saveFilterMap({title:urlValue,value:  multiArr.join(',') }))
    } else {
      dispatch(saveFilterMap({title:urlValue,value:valueArr.value}))
    }
  };

  return (
    <div className="container p-2">
      <div className="border-1 w-full grid grid-cols-3">
        {columns?.map((col, index) => (
          <Select
            ref={selectRef}
            key={index}
            onChange={(state, action) =>
              handleChange(state, action, col.urlValue)
            }
            className="mx-2 my-2 w-2/3 bg-slate-200  text-gray-900 text-sm rounded-lg block"
            placeholder={col.defaultValue}
            isDisabled={col?.values?.value}
            options={col?.dataType === "opt" ? col?.values : col?.values}
            isMulti={col.dataType === "multi" || col.dataType === "opt"}
            
          />
        ))}

        {/* optgroup daylar */}
        <button
          onClick={showFilter}
          
          className="w-[80px] mx-2 my-2 h-[40px] cursor-pointer  bg-green-500 hover:bg-green-700 text-white font-bold py-2 px-4 rounded "
        >
          Filter
        </button>
        {/* <button
          onClick={handleClearValue}
          className="w-2/3 mx-2 my-2 h-[40px]  bg-green-500 hover:bg-green-700 text-white font-bold py-2 px-4 rounded "
        >
          clear filter
        </button> */}
      </div>
    </div>
  );
};

export default UniversalFilter;
