import React, { useEffect } from "react";
import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogTitle from "@mui/material/DialogTitle";
import { DialogContent } from "@mui/material";
import { useDispatch, useSelector } from "react-redux";
import { changeConfirmModalVisible } from "../redux/slices/UModalSlice/UModalSlice";
import {
  changeCurrentTerritoryItm,
  setLatitude,
  setLongitude,
} from "../redux/slices/Territory/TerritorySlice";
import { updateCurrentClient } from "../redux/slices/clients/ClientSlice";
import { changeCurrentCustomerCategory } from "../redux/slices/CustomerCategory/CustomerCategorySlice";

const ConfirmClose = () => {
  const confirmModalVisible = useSelector(
    (state) => state.uModal.confirmModalVisible
  );
  const globalModalDispatch = useSelector(
    (state) => state.uModal.globalModalDispatch
  );
  const { columns } = useSelector((state) => state.table);

  const dispatch = useDispatch();

  useEffect(() => {
    // dispatch(setModalColumns(columns));
  }, [columns, dispatch]);

  const handleClose = (e, reason) => {
    if (reason === "backdropClick") {
    } else if (reason === "ha") {
      dispatch(changeConfirmModalVisible(false));
      dispatch(globalModalDispatch);
      dispatch(setLatitude(""));
      dispatch(setLongitude(""));
      dispatch(updateCurrentClient(""));
      dispatch(changeCurrentCustomerCategory(""));
      dispatch(changeCurrentTerritoryItm(""));
    } else {
      dispatch(changeConfirmModalVisible(false));
    }
  };

  return (
    <div>
      <Dialog
        open={confirmModalVisible}
        onClose={handleClose}
        maxWidth="sm"
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          {"diqqat save qilmasangiz ma'lumotlar o'chib ketadi!"}
        </DialogTitle>
        <DialogContent className="text-center">
          <h1 className="text-2xl">rozimisiz?</h1>
        </DialogContent>
        <DialogActions>
          <Button color="error" onClick={(e) => handleClose(e, "yoq")}>
            yoq
          </Button>
          <Button
            color="success"
            onClick={(e) => handleClose(e, "ha")}
            autoFocus
          >
            ha
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
};

export default ConfirmClose;
