import React from "react";
import NavBar from "./NavBar";
import SideBar from "./SideBar";
import { Outlet } from "react-router-dom";
const Admin = () => {
  return (
    <div>
      <NavBar />
      <div className="flex">
        <div className="h-[100vh]">
          <SideBar />
        </div>
        <div className="w-full">
          <Outlet />
        </div>
      </div>
    </div>
  );
};

export default Admin;
