import React, { useEffect } from "react";
import logo from "../img/logo.jpg";
import question from "../img/question.png";
import notification from "../img/notification.png";
import gps from "../img/gps.png";
import date from "../img/calendar.png";
import DropDown from "./DropDown";
import { useDispatch, useSelector } from "react-redux";
import {
  fetchPhoneAndNumber,
} from "../redux/slices/dashboard/DashboardSlice";
import { Link } from "react-router-dom";

const NavBar = () => {
  const dispatch = useDispatch();
  const { data } = useSelector((state) => state.dashboard);

  useEffect(() => {
    dispatch(fetchPhoneAndNumber());
  }, [dispatch]);

  return (
    <div className="h-[80px] w-full bg-gray-600 flex justify-center items-center sticky top-0 z-[990] ">
      <div className="w-[97%] flex justify-between items-center">
        <Link to={"/admin"}>
        <img className="w-16 h-16 rounded-full" src={logo} alt="logo" />
        </Link>
        <ul className="flex gap-12 text-white font-bold">
          <li className="my-auto cursor-pointer">Supervisor</li>
          <li className="my-auto cursor-pointer">Sales</li>
          <li className="my-auto cursor-pointer">
            <select className="bg-gray-600 cursor-pointer" name="" id="">
              <option value="">Clash register</option>
            </select>
          </li>
          <li className="flex my-auto cursor-pointer">
            <img src={gps} alt="gps" />
            <select className="bg-gray-600 cursor-pointer" name="" id="">
              <option value="">GPS</option>
            </select>
          </li>
          <li className="flex justify-center gap-2 items-center w-40 h-16 rounded-sm cursor-pointer bg-green-500 hover:bg-green-700">
            <img
              className="w-10 h-18 bg-white rounded-full"
              src={question}
              alt="question"
            />
            <p>Online Help</p>
          </li>
        </ul>
        {data && (
          <div className="flex items-center gap-4 text-white">
            <b className="flex items-center gap-3 bg-blue-500 p-4 hover:bg-blue-700 rounded-sm">
              <img src={date} alt="date" />
              {data.currentDate}
            </b>
            <p className="font-bold text-md">{data.supportPhone}</p>
          </div>
        )}
        <div className="flex gap-4">
          <img className="w-7 h-7 cursor-pointer" src={notification} alt="" />
          <DropDown />
        </div>
      </div>
    </div>
  );
};

export default NavBar;
