import {call, put, select, takeLatest} from "redux-saga/effects";
import {getAllCustomerCategorySuccess, getAllLocations, getClientsSuccess, updateCurrentClient} from "../slices/clients/ClientSlice";
import Apicall from "../../api/Apicall";

// save client
function* workSaveClient (action) {
    const tableState = yield select((state) => state.table);
    const currentStates = yield select((state) => state.territory);
    const clientStates = yield select((state) => state.client);
     let {address,category,referencePoint,companyName,lat,long,name,telephone,territory,tin,id,active}=action.payload
     let obj={
        name: name,
        address: address,
        telephone: telephone,
        tin:tin,
        referencePoint:referencePoint,
        companyName: companyName,
        categoryId:category.value,
        territoryId: territory.value,
        longitude: long,
        latitude: lat,
        active:active
      }

    try {
          if(clientStates.currentClient!==""){
            const response = yield call(Apicall, "/api/client", "PUT",{
                id:clientStates.currentClient.id,
                name: name,
                address: address,
                telephone: telephone,
                tin:tin,
                referencePoint:referencePoint,
                companyName: companyName,
                categoryId:category.value,
                territoryId: territory.value,
                longitude: long,
                latitude: lat,
                active:active
              } );
            yield put(updateCurrentClient(""))
          }else{
            const res = yield call(Apicall, "/api/client", "POST", obj);
            yield put(updateCurrentClient(""))
          }
          yield put({type:"territory/getTerritory", 
            payload: {
              pageSize: tableState.pageSize,
              page: tableState.currentPage,
              totalElements: currentStates.allTerritorys,
              active: tableState.tableActive,
              search: tableState.searchInpValue,
              url: tableState.url,
            },
          });
    } catch (error) {}
}


function* workGetClients (action) {
    try {
    const res = yield call(Apicall, "/api/client/filter", "GET");
    yield put(getClientsSuccess(res.data.content));
    } catch (error) {}
}

// update
function* workUpdateClients (action) {
    try {
        const res = yield call(Apicall, "/api/client", "GET");
        yield put(getClientsSuccess(res.data));
    } catch (error) {}
}

function* workGetAllCustomerCategory () {
    try {
        const res = yield call(Apicall, "/api/customer_category/all", "GET");
        yield put(getAllCustomerCategorySuccess(res.data));
    } catch (error) {}
}

function* workGetExcelFile (action) {
    try {
        const response = yield call(Apicall, "/api/client/excel", "GET","",action.payload);
        if (response.status === 200) {
            const blob = new Blob([response.data], {type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'});
            const filename = "my-workbook.xlsx";
            const link = document.createElement("a");
            link.href = window.URL.createObjectURL(blob);
            link.setAttribute("download", filename);
            document.body.appendChild(link);
            link.click();
        } else {
            console.log("Error downloading excel file: ", response.status);
        }
    } catch (error) {}
}
function* workGetAllMapLocation () {
    try {
        const res = yield call(Apicall, "/api/client/map", "GET");
        yield put(getAllLocations(res.data));
    } catch (error) {
        
    }
}

// saga
export function* clientSaga () {
    yield takeLatest("client/saveClient", workSaveClient);
    yield takeLatest("client/getClients", workGetClients);
    yield takeLatest("client/updateClients", workUpdateClients);
    yield takeLatest("client/getExcelFile", workGetExcelFile);
    yield takeLatest("client/getAllCustomerCategory", workGetAllCustomerCategory);
    yield takeLatest("client/getAllMap", workGetAllMapLocation);
}
