import { all, fork } from "redux-saga/effects";
import { login } from "../sagas/LoginSaga";
import DashboardSaga from "./DashboardSaga";
import { territorySaga } from "./TerritorySaga";
import { table } from "./TableSaga";
import { filter } from "./FilterSaga";
import { clientSaga } from "./ClientSaga";
import {CustomerCategorySaga} from "./CustomerCategorySaga"
import {settingsSaga} from "./SettingsSaga"
import {companyProfileSaga} from "./CompanyProfileSaga"

export function* rootSaga() {
  yield all([
    fork(login),
    fork(DashboardSaga),
    fork(territorySaga),
    fork(table),
    fork(filter),
    fork(clientSaga),
    fork(CustomerCategorySaga),
    fork(settingsSaga),
    fork(companyProfileSaga)
  ]);
}
